import { Component, OnInit } from "@angular/core";

@Component({
    selector: 'multi-layer',
    templateUrl: './multi-layer.component.html',
    styleUrls: ['./multi-layer.component.scss']
})

export class MultiLayerComponent implements OnInit {
    allNotifInfo = [
        {
            id: 0,
            text: 'There is nothing left for me top say. It`s just simple tic tac toe game with custom length. You can put a number as the game length maximum as big as your screen can handle, and minimum 3. I know my code is full of bugs, but please just put some number and enjoy the game! Reset button will appear on the game, in case you need it.',
            colorCode: '#B46464',
            fontColor: '#E0E0E0',
            icon: 'sentiment_satisfied_alt'
        },
        {
            id: 1,
            text: 'This is merely an example of how I display multiple notification banner. I`m not a UI/ UX Designer so I`m not sure if this design is good enough as a banner, but at least this is my origin idea. If someone, somewhere already used this design, it is just merely coincidence.',
            colorCode: '#8B8D00',
            fontColor: '#E0E0E0',
            icon: 'play_circle_filled'
        },{
            id: 2,
            text: 'There is nothing left for me top say. It`s just simple tic tac toe game with custom length. You can put a number as the game length maximum as big as your screen can handle, and minimum 3. I know my code is full of bugs, but please just put some number and enjoy the game! Reset button will appear on the game, in case you need it.',
            colorCode: '#008D02',
            fontColor: '#E0E0E0',
            icon: 'sentiment_satisfied_alt'
        },
        {
            id: 3,
            text: 'This is merely an example of how I display multiple notification banner. I`m not a UI/ UX Designer so I`m not sure if this design is good enough as a banner, but at least this is my origin idea. If someone, somewhere already used this design, it is just merely coincidence.',
            colorCode: '#00898D',
            fontColor: '#E0E0E0',
            icon: 'play_circle_filled'
        },{
            id: 4,
            text: 'There is nothing left for me top say. It`s just simple tic tac toe game with custom length. You can put a number as the game length maximum as big as your screen can handle, and minimum 3. I know my code is full of bugs, but please just put some number and enjoy the game! Reset button will appear on the game, in case you need it.',
            colorCode: '#00048D',
            fontColor: '#E0E0E0',
            icon: 'sentiment_satisfied_alt'
        },
        {
            id: 5,
            text: 'This is merely an example of how I display multiple notification banner. I`m not a UI/ UX Designer so I`m not sure if this design is good enough as a banner, but at least this is my origin idea. If someone, somewhere already used this design, it is just merely coincidence.',
            colorCode: '#C6018D',
            fontColor: '#E0E0E0',
            icon: 'play_circle_filled'
        },
    ]
    ngOnInit(): void {
        this.allNotifInfo.forEach(
            item => {
                let newText = '';
                for(let i = 0; i < 13; i++){
                    newText += `This is Notification number ${item.id + 1}. `
                }
                item.text = newText;
            }
        )
    }
    clickNotif(notifIndex){
        if(notifIndex != this.allNotifInfo.length - 1){
            let listWrapper = document.getElementsByClassName('notification-wrapper');
            Array.from(listWrapper).forEach(
                elm => {
                    let allClassArray = elm.className.split(' ');
                    allClassArray.splice(allClassArray.findIndex(str => str.includes('notif-')), 1, 'reset-top');
                    elm.className = allClassArray.join(' ');
                }
            )
            setTimeout(()=>{
                let lastNotif = this.allNotifInfo[this.allNotifInfo.length - 1];
                while(lastNotif.id != notifIndex){
                    let tempElement = lastNotif;
                    this.allNotifInfo.pop();
                    this.allNotifInfo = [tempElement].concat(this.allNotifInfo);
                    lastNotif = this.allNotifInfo[this.allNotifInfo.length - 1];
                }
                this.allNotifInfo.forEach(
                    (notif, idx) => {
                        notif.id = idx;
                    }
                )
            }, 300)
        }
    }
    closeBanner(notifIndex){
        this.allNotifInfo.splice(notifIndex, 1);
    }

    topStyle(notifIndex: number){
        let arrayLength = this.allNotifInfo.length;
        if(arrayLength > 3){
            if(arrayLength - notifIndex > 2){
                return 'notif-1' + (arrayLength - notifIndex > 3?' no-shadow':'');
            }else{
                return 'notif-'+ (notifIndex+4 - arrayLength);
            }
        }else{
            return 'notif-'+(notifIndex+1);
        }
    }
}